import http from "../http";

const apiRequest = {

  getCatalogueList (pagination) {
    return http.get('/luochen/catalogue/list', pagination)
  },

  getSpecialList () {
    return http.get('/luochen/special/list', {})
  },

  getBlogList (pagination) {
    return http.get('/luochen/blog/list', pagination)
  },

  getToolList () {
    return http.get('/luochen/tool/list', {})
  }

}

export default apiRequest
