<template>
  <div>
    <div class="main-tools-box">
      <a-row :gutter="[16,16]">

        <a-col :span="6" v-for="(tool, index) in toolList" :key="index">
          <div class="main-tools-item-box">
            <div class="main-tools-item-img">
              <img src="https://gong_zheng_ping.gitee.io/images/14.jpg" alt=""/>
            </div>
            <div class="main-tools-item-text">
              <a :href="tool.link" target="_blank">{{ tool.name }}</a>
            </div>
          </div>
        </a-col>

      </a-row>
    </div>
  </div>
</template>

<script>
import apiRequest from "../../../request/api/ApiRequest";

export default {
  name: "MainToolListBar",
  data () {
    return {
      toolList: []
    }
  },
  methods: {
    initMainToolListView () {
      apiRequest.getToolList().then(res => {
        if (res.code === 2000) this.toolList = res.data
      })
    },
    showImgHandle (imgPath) {
      console.log("'" + imgPath + "'")
      // return require('../../../assets/tool-img/youdao-img.png')
      return imgPath
    }
  }
}
</script>

<style scoped>
.main-tools-box{width: 100%;height: 540px;padding: 1em;overflow-y: scroll;}
.main-tools-box::-webkit-scrollbar{width: 0}
.main-tools-box .main-tools-item-box{
  width: 100%;height: 200px;transition: .43s;border: #eaeaea solid 1px;
  border-radius: 4px;overflow: hidden;
}
.main-tools-box .main-tools-item-box:hover{
  box-shadow: 0 15px 35px rgba(50,50,93,.1),0 5px 15px rgba(0,0,0,.07);
}
.main-tools-box .main-tools-item-box .main-tools-item-img {width: 100%;height: 160px;}
.main-tools-box .main-tools-item-box .main-tools-item-img img{width: 100%;height: 100%;}
.main-tools-box .main-tools-item-box .main-tools-item-text a{
  display: block;width: 100%;height: 40px;line-height: 40px;text-align: center;
  font-size: 16px;cursor: pointer;
}

</style>
